import React from 'react';
import { NavLink } from 'react-router-dom';
import './styles/Navigation.css';
import { useTranslation } from 'react-i18next';

export default function Navigation() {
    const { t } = useTranslation();
    return (
        <div className="navi">
            <p>menu</p>
            <ul>
                <li><NavLink exact to="/">{t('Start')}</NavLink></li>
                <li><NavLink exact to="/oferta">{t('Oferta')}</NavLink></li>
                <li><NavLink exact to="/omnie">{t('O mnie')}</NavLink></li>
                <li><NavLink exact to="/kontakt">{t('Kontakt')}</NavLink></li>
            </ul>
        </div>
    )
}